/*
==============================
Base styles
==============================
*/

// Header

.card {
  @extend %card;
  @extend %m-top;
}

nav {
  @extend %center;
  position: relative;

  @include tablet {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

}

.logo {
  width: em(56px);
}

.main-nav {
  @extend %m-top;
  display: none;
  text-align: right;

  @include tablet {
    display: flex;
    margin: 0;
  }

  & a {
    @extend %p-5;
    display: block;
    color: $color-secondary;
    font-weight: $font-weight--bold;
    text-transform: capitalize;
    transition: color .3s ease-out;

    &:hover {
      color: $color-btn;
    }

    &:active {
      color: darken($color-btn, 20%);
    }
  }
}

.main-nav li {
  margin-bottom: em(10px);

  @include tablet {
    margin-bottom: 0;
    margin-left: em(20px);
  }
}

.nav-btn {
  position: absolute;
  top: em(15px);
  right: em(15px);
  width: 24px;
  height: 24px;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;

  @include tablet {
    display: none;
  }
}

.nav-btn span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #1f1f1f;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.nav-btn span:nth-child(1) {
  top: 0px;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-btn span:nth-child(2) {
  top: 0.6875em;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-btn span:nth-child(3) {
  bottom: 0;
  -webkit-transform-origin: left center;
  -moz-transform-origin: left center;
  -o-transform-origin: left center;
  transform-origin: left center;
}

.nav-btn.open span:nth-child(1) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
  top: 0.125em;
}

.nav-btn.open span:nth-child(2) {
  width: 0%;
  opacity: 0;
}

.nav-btn.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
  bottom: 0.1875em;
}

// Gallery

.gallery {
  @include tablet {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.photo-container {
  perspective: 800px;
  margin: 0 0 em(25px) 0;

  @include tablet {
    flex-basis: 48.5%;
    margin-bottom: 2.5%;
  }
  @include desktop {
    flex-basis: 31.666666666666667%;
  }
}

.photo {
  @extend %b-shadow;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 1s cubic-bezier(.55, -.62, .27, 1.2);
  transform-style: preserve-3d;
}

.photo:hover {
  transform: rotateY(-180deg);
}

.side-b {
  @extend %p-15;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $color-btn;
  transform: rotateY(180deg);

  @include tablet {
    padding: em(10px);
  }

}

.side-a,
.side-b {
  -webkit-backface-visibility: hidden;
}

.side-b p,
.side-b h3 {
  margin: em(10px);
  color: $white;
  text-align: center;

  @include tablet {
    margin: 0;
  }
}

.side-b p {
  @include tablet {
    font-size: .8rem;
  }
}
.photo-links {
  text-align: center;
  margin-top: em(5px);

  & a {
    display: inline-block;
    min-width: 100px;
    margin: em(10px);
    padding: em(5px);
    background: darken($color-btn, 20%);
    border-radius: em(25px);
    text-transform: uppercase;
    transition: background .3s ease-out;

    @include tablet {
      font-size: .8rem;
    }

    &:hover {
      background: darken($color-btn,30%);
    }
  }
}

.photo-skills {
  display: flex;
  justify-content: flex-end;
  position: absolute;
  right: em(10px);
  bottom: em(10px);
}

.photo-skills p {
  @include font(.8rem, 1.5,$font-weight--bold, darken($color-btn, 20%));
  text-transform: uppercase;
  margin: 0 0 0 em(5px);
  @include tablet {
    font-size: .6rem;
  }
}
