/*
==============================
Base styles
==============================
*/

*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  @include linearGradient($geyser, $catskill-white);
}

ul {
  list-style: none;
  padding-left: 0;
}

.wrapper {
  @extend %container;
}

img,
svg {
  display: block;
  width: 100%;
}

// Text styles

h1 {
  @include font(em(36px), 1.5, $font-weight--bold, $color-secondary);
  font-family: $font-family--secondary;
  margin-bottom: 0;
}

h3 {
  @include font(em(24px), 1.5, $font-weight--bold, $color-secondary);
  font-family: $font-family--secondary;
}

p {
  @include font(em($base__font-size), em($base__line-height), $color: $color-primary);
  font-family: $font-family--primary;
  margin-top: 0;
}

a {
  @include font(em($base__font-size), em($base__line-height), $color: $color-btn);
  text-decoration: none;
  font-family: $font-family--primary;
}
